import React from 'react';

const Section = props => (
    <div className={`section ${props.headerClass}`}>
        <h2 data-animate={`${props.side1}`}>{props.title}</h2>
        <img className={`${props.firstClass}`} src={require(`${props.firstSrc}`)} alt={`${props.firstAlt}`} data-animate={`${props.side2}`} />        
        <img className={`${props.secondClass}`} src={require(`${props.secondSrc}`)} alt={`${props.secondAlt}`} data-animate={`${props.side4}`} />
    </div>
)

export default Section