import React from 'react';

const Card = props => (
    <div className={`card ${props.class}`} data-animate={`${props.side}`}>
        <img src={require(`${props.src}`)} alt={`${props.alt}`} />
        <h3>{props.title}</h3>
        <p className={`card-description`}>
            {props.content}
        </p>
    </div>
)

export default Card